<template>
  <div>
    <div class="breadcrumb-preview dy-breadcrumb-div">
      <el-breadcrumb :separator="''"
        :style='{ "width": "1200px", "margin": "0 auto", "fontSize": "14px", "lineHeight": "1" }'>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="detail-preview"
      :style='{ "width": "1200px", "padding": "0 0 24px", "margin": "0px auto", "position": "relative" }'>
      <div class="dy-add-detail-attr-div">
        <el-form class="add-update-preview" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="100px">
          <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="loginname">
          <el-input v-model="ruleForm.loginname" placeholder="" clearable :readonly="ro.loginname"></el-input>
        </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="loginpw">
          <el-input v-model="ruleForm.loginpw" placeholder="" clearable :readonly="ro.loginpw"></el-input>
        </el-form-item>
        <el-form-item :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }' label="" prop="type">
          <el-select v-model="ruleForm.type" placeholder="" @change="typeChange($event)" disabled>
            <el-option v-for="(item, index) in typeOptions" v-bind:key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
      </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="typename">
          <el-input v-model="ruleForm.typename" placeholder="" clearable :readonly="ro.typename"></el-input>
        </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" placeholder="" clearable :readonly="ro.xingming"></el-input>
        </el-form-item>
        <el-form-item :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }' label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option v-for="(item, index) in xingbieOptions" v-bind:key="index" :label="item" :value="item"></el-option>
          </el-select>
      </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="nianling">
          <el-input v-model="ruleForm.nianling" placeholder="" clearable :readonly="ro.nianling"></el-input>
        </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="address">
          <el-input v-model="ruleForm.address" placeholder="" clearable :readonly="ro.address"></el-input>
        </el-form-item>
        <el-form-item
          :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }'
          label="" prop="dianhua">
          <el-input v-model="ruleForm.dianhua" placeholder="" clearable :readonly="ro.dianhua"></el-input>
        </el-form-item>
        <el-form-item v-show="false" :style='{ "border": "0px solid #dfdfdf", "padding": "10px", "boxShadow": "0px 0px 0px #eee", "margin": "0 0 8px 0", "borderRadius": "8px", "background": "none" }' label="" prop="shenhe">
          <el-select v-model="ruleForm.shenhe">
            <el-option v-for="(item, index) in shenheOptions" v-bind:key="index" :label="item" :value="item">
            </el-option>
          </el-select>
      </el-form-item>

          <el-form-item :style='{ "padding": "0", "margin": "20px 0 20px 0" }'>
            <el-button class="dy-add-button" type="primary" @click="onSubmit"></el-button>
            <el-button class="dy-back-button" @click="back()"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      baseUrl: this.$config.baseUrl,
      xingbieOptions: [],
      typeOptions: [],
      shenheOptions: [],
      users: { loginname: localStorage.getItem('adminName') },
      ro: {
        loginname: false,
        loginpw: false,
        type: false,
        typename: true,
        xingming: false,
        xingbie: false,
        nianling: false,
        address: false,
        dianhua: false,
        shenhe: false,

      },
      ruleForm: {
        loginname: '',
        loginpw: '',
        type: '2',
        typename: '',
        xingming: '',
        xingbie: '',
        nianling: '',
        address: '',
        dianhua: '',
        shenhe: 'no',

      },
      rules: {
        loginname: [{ required: true, message: '', trigger: 'blur' },],
        loginpw: [{ required: true, message: '', trigger: 'blur' },],
        type: [{ required: true, message: '', trigger: 'blur' },],
        typename: [{ required: true, message: '', trigger: 'blur' },],
        xingming: [{ required: true, message: '', trigger: 'blur' },],
        xingbie: [{ required: true, message: '', trigger: 'blur' },],
        nianling: [{ required: true, message: '', trigger: 'blur' },],
        address: [{ required: true, message: '', trigger: 'blur' },],
        dianhua: [{ required: true, message: '', trigger: 'blur' }, { validator: this.$validate.isMobile, trigger: 'blur' },],
        shenhe: [{ required: true, message: '', trigger: 'blur' },],

      },

    };
  },
  computed: {

  },
  created() {
    this.init();

  },
  methods: {
    //yyyy-MM-dd  yyyy-MM-dd hh:mm:ss
    getCurrentTime(type) {
      var date = new Date();
      var year = date.getFullYear();
      /* 00
       * 100   09:11:05
       * */
      var month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
      var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var hours = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      if (type && type == 1) {
        // 
        return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
      } else {
        return year + "-" + month + "-" + day
      }
    },
    typeChange(value) {
      // 
      console.log(value);
      let obj = {};
      obj = this.typeOptions.find((item) => { // userList
        return item.value === value; // 
      });
      console.log(obj.label);
      this.ruleForm.typename = obj.label;
    },
    // 
    init() {
      this.shenheOptions = "no,yes".split(',')
      this.xingbieOptions = ",".split(',')
      this.typeOptions = [{ value: '0', label: '' }, { value: '2', label: '' }]
    },

    // 
    onSubmit() {
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http.post('users/add', this.ruleForm).then(res => {
            if (res.data.code == 0) {
              this.$message({
                message: ',',
                type: 'success',
                duration: 1500,
                onClose: () => {
                  this.$router.go(-1);
                }
              });
            } else {
              this.$message({
                message: res.data.message,
                type: 'error',
                duration: 1500
              });
            }
          });
        }
      });
    },
    // uuid
    getUUID() {
      return new Date().getTime();
    },
    // 
    back() {
      this.$router.go(-1);
    },
 
 
  }
};
</script>

